@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* cssrest */
body {
  font-family: 'Roboto', 'Noto Sans', sans-serif;
  font-size: 16px;
  line-height: 1.5; }
  body h2 {
    line-height: 1.5;
    font-size: 48px;
    padding-top: 60px;
    padding-bottom: 40px;
    color: #650300;
    text-align: center; }
  body h3 {
    font-size: 48px;
    line-height: 72px;
    color: #AA0601;
    font-weight: bold;
    font-style: italic; }

.header {
  width: 100%;
  background-color: #650300;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header .logo {
    margin: 10px; }
  .header ul {
    width: 500px;
    list-style: none;
    display: flex;
    justify-content: space-around; }
    .header ul a {
      color: white;
      text-decoration: none; }
    .header ul a:link {
      /* 設定還沒有瀏覽過的連結 */
      text-decoration: none; }
    .header ul a:hover {
      color: burlywood;
      text-decoration: none; }

.banner {
  color: #650300;
  position: relative; }
  .banner .ban-img {
    max-width: 100%; }
  .banner .banner-item {
    position: absolute;
    top: 300px;
    right: 300px; }
    .banner .banner-item p {
      font-size: 36px;
      line-height: 1.5; }
    .banner .banner-item h1 {
      font-size: 64px;
      line-height: 1.5;
      font-family: 'Noto Sans TC', sans-serif; }

/* 用專業的心，做專業的事 */
.item1 {
  background: #AA06010D;
  margin: 0 auto; }
  .item1 ul {
    display: flex;
    justify-content: center; }
  .item1 li {
    width: 255px;
    margin-right: 15px;
    margin-left: 15px; }
    .item1 li h3 {
