      height: 100px;
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center; }
    .item1 li .im100 img {
      display: block;
      align-items: center; }

.item2_care {
  display: flex;
  max-width: 1140px;
  margin: auto; }

.footer {
  background: #AA0601;
  color: #ffffff;
  padding: 24px 0 40px; }
  .footer .container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #FFF;
    padding-bottom: 24px; }
  .footer .nav {
    display: flex; }
  .footer .footer-nav {
    font-size: 20px;
    line-height: 1.5;
    color: #ffffff;
    padding: 20px; }
    .footer .footer-nav .footer-link {
      text-decoration: none;
      color: #ffffff;
