  .footer .footer-copyright {
    max-width: 1140px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    color: #fff;
    margin-top: 32px; }
    .footer .footer-copyright ul {
      display: flex; }
      .footer .footer-copyright ul li a {
        text-decoration: none;
        color: #ffffff;
        margin-left: 48px;
        font-size: 14px; }
